import React, { Component } from 'react';
import { Modal, InputNumber, Select, Row, Col, Button, Input} from 'antd';
const Option = Select.Option;

class ChartOptions extends Component {
		constructor(props){
	      super(props);
	      
	      this.state = {
	      		visible: false,
	      		chartOptions: {}	
	      }
	      this.owner = this.props.owner;
	  }
	  
	  componentWillReceiveProps(nextProps) {
	  		let {visible, chartOptions} = nextProps;
	  		this.setState({visible, chartOptions});
	  }
	  
	  changeOption(index, e){
	  		let {xOptions} = this.state;
	  		xOptions[index] = e.target.value;
	  		this.setState({xOptions});
	  }
	  
	  handleOk(){
	  		this.owner.setState({chartOptions: this.state.chartOptions, chartOptionsVisible: false});
	  }
	  
	  handleCancel(){
	  		this.owner.setState({chartOptionsVisible: false});
	  }
	  
	  setMinValue(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.minValue = e.target.value;
	  		this.setState({chartOptions});
	  }
	  
	  setMaxValue(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.maxValue = e.target.value;
	  		this.setState({chartOptions});
	  }
	  
	  setTrendMethod(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendMethod = value;
	  		this.setState({chartOptions});	  		
	  }
	  
	  setTrendType(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendType = value;
	  		this.setState({chartOptions});	  		
	  }	
	  
	  setTrendName(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendName = e.target.value;
	  		this.setState({chartOptions});	  		
	  }		    

	  setTrendDecimal(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendDecimal = value;
	  		this.setState({chartOptions});	  		
	  }	
	  	  
	  render() {  		
		    let {chartOptions} = this.state;
		    
		    return (
		      <div>
		        <Modal title="" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)}
						footer={[
		            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}> </Button>,
		            <Button key="submit" type="primary" onClick={this.handleOk.bind(this)}>
		               
		            </Button>,
	          ]} width="600px"		        
		        >
        				<Row gutter={16} style={{margin: '5px'}}>
        						<Col span={5}><label>  <InputNumber value={chartOptions.minValue} onChange={this.setMinValue.bind(this)}/></label></Col>
        						<Col span={5}><label>  <InputNumber value={chartOptions.maxValue} onChange={this.setMaxValue.bind(this)}/></label></Col>
								</Row>
								<Row gutter={16} style={{margin: '5px'}}>
										<Col span={24}><label>
													<Select value={chartOptions.trendMethod} onChange={this.setTrendMethod.bind(this)} style={{width:'120px'}}>
															<Option value="" key=""></Option>
															<Option value="avg" key="avg"></Option>
													</Select>
											    <Select value={chartOptions.trendType} onChange={this.setTrendType.bind(this)} style={{width:'120px'}}>
	      											<Option value="" key=""></Option>
											      	<Option value="column" key="column"></Option>
											      	<Option value="stackedColumn" key="stackedColumn"></Option>
											      	<Option value="bar" key="bar"></Option>
											      	<Option value="stackedBar" key="stackedBar"></Option>
											      	<Option value="line" key="line"></Option>
											      	<Option value="pie" key="pie"></Option>
											      	<Option value="area" key="area"></Option>
											      	<Option value="stackedArea" key="stackedArea"></Option>
											      	<Option value="funnel" key="funnel"></Option>
											      	<Option value="radar" key="radar"></Option>
											      	<Option value="gauge" key="gauge"></Option>
											    </Select>
											    <Input value={chartOptions.trendName} onChange={this.setTrendName.bind(this)} placeholder='' style={{width: 100}} title=''/>
											    <InputNumber min={0} max={6} value={chartOptions.trendDecimal} onChange={this.setTrendDecimal.bind(this)} placeholder='' style={{width: 70}} title=''/>
										</label></Col>
								</Row>
		        </Modal>
	        </div>
	      )
		}	  
}

export default ChartOptions;