import React, { Component } from 'react';
import { Modal, Button, Radio, Input, Row, Col, Checkbox, Select } from 'antd';
import XOptions from './XOptions';
import YOptions from './YOptions';
import ChartOptions from './ChartOptions';
import $ from 'jquery';
const Highcharts = require('highcharts');
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const Option = Select.Option;

class ChartProperty extends Component {
		constructor(props){
	      super(props);
	      this.owner = this.props.owner;
	      this.state = {
	      		xOptionsVisible: false,
	      		yOptionsVisible: false,
	      		chartOptionsVisible: false,
	      		visible: false,
	      		items: [],//
	      		
	      		orientation: '2',//
	      		dataRegion: [], //
	      		categoryRegion: [],//
	      		title: '',//
	      		x: '',//x
	      		y: '',//y
	      		is3D: false,//3D
	      		chartType:'column',//
	      		xOptions: [],//x
	      		yOptions: [],//y
	      		chartOptions: {},//
	      }
	  }
	  handleOk() {
	  		let {activeChart} = this.props;
	  		delete this.state.xOptionsVisible;
	  		delete this.state.yOptionsVisible;
	  		delete this.state.chartOptionsVisible;
	  		delete this.state.visible;
	  		delete this.state.items;
	  		
	  		
	  		if(activeChart){
	  				activeChart.config = {...this.state};
	  				window.setTimeout(()=> {this.owner.owner.setState({activeChart}); this.owner.owner.excel.render();}, 0);
	  		}else{
	  				let selected = this.owner.owner.excel.getSelectedLast();
	  				let {activeSheet} = this.owner.owner.state;
	  				if(!activeSheet.charts) activeSheet.charts = [];
	  				activeSheet.charts.push({row: selected[0], col: selected[1], config: {...this.state}});
	  				window.setTimeout(()=> {this.owner.owner.setState({activeSheet}); this.owner.owner.excel.render();}, 0);
	  		}
		    window.setTimeout(()=> {this.handleCancel();}, 50);
	  }
	  handleCancel(e) {
		    this.owner.setState({showChartPropertyFlag: false});
		    window.setTimeout(()=>this.owner.owner.setState({activeChart: null}), 0);
	  }
	  componentWillReceiveProps(nextProps) {
	  		let {visible, activeChart, items} = nextProps;
	  		if(activeChart){
	  				this.setState({visible,...activeChart.config, items});
	  		}else{
	  				this.setState({visible,	
	  						orientation: '2',//
			      		dataRegion: [], //
			      		categoryRegion: [],//
			      		title: '',//
			      		x: '',//x
			      		y: '',//y
			      		is3D: false,//3D
			      		chartType:'column',//
			      		xOptions: [],//x
			      		yOptions: [],//y
			      		chartOptions: {},//
			      		items
	  		});
	  	}
	  }
	  changeDataRegion(e){
	  		let value = e.target.value;
	  		if(value && value.search(/^([A-Z]+[0-9]+)(:)([A-Z]+[0-9]+)$/)!==-1){
	  				
	  		}else if(value!==''){
	  				alert('A2:B3');
	  				value = '';
	  		}
	  		
	  		let {yOptions} = this.state;
	  		if(value!==''){
	  				let region = this.owner.owner.excel.regionToRef(value), nth = 0;
	  				if(this.state.orientation === '1'){
	  						for(let row = region[0]; row <= region[2]; row++){
	  								let merge = this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection && this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection.get(row, region[1]);
	  								if(!merge || (merge.row === row && merge.col === region[1])){
	  										if(yOptions.length < (nth + 1)){
	  												yOptions.push({name: '' + (nth + 1), type: ''});	
	  										}
	  										nth++;
	  								}
	  						}
	  				}else{
	  						for(let col = region[1]; col <= region[3]; col++){
	  								let merge = this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection && this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection.get(region[0], col);
	  								if(!merge || (merge.row === region[0] && merge.col === col)){
	  										if(yOptions.length < (nth + 1)){
	  												yOptions.push({name: '' + (nth + 1), type: ''});	
	  										}
	  										nth++;
	  								}
	  						}	  					
	  				}
	  				yOptions.filter((option, index) => index < nth);
	  		}
	  		this.setState({dataRegion: value, yOptions});
	  }
	  changeCategoryRegion(e){
	  		let value = e.target.value;
	  		if(value && value.search(/^([A-Z]+[0-9]+)(:)([A-Z]+[0-9]+)$/)!==-1){
	  				
	  		}else if(value!==''){
	  				alert('A1:B1');
	  				value = '';
	  		}
	  		let {xOptions} = this.state;
	  		if(value!==''){
	  				let region = this.owner.owner.excel.regionToRef(value);
						let datas = this.owner.owner.excel.getData(region[0], region[1], region[2], region[3])[0];
						let categories = [];
						datas.map(dat => categories = categories.concat(dat));
						categories.map((dat, nth) => {
								if(xOptions.length < (nth + 1)){
										xOptions.push('' + (nth + 1));
								}
						});
						xOptions.filter((option, index) => index < categories.length);
				}
	  		this.setState({categoryRegion: value, xOptions});
	  }
	  
	  render() { 
	  		let {dataRegion, categoryRegion} = this.state;
	  		let {items} = this.props;	
	  		items = Object.keys(items).map(key=> items[key]);
	  		let the_item_name = "";
	  		if(dataRegion.length > 0){
	  			 let the_item = items.filter(item => item.id === dataRegion[0]);
	  			 the_item_name = the_item[0]['name'];
	  		}else if(categoryRegion.length > 0){
	  				let the_item = items.filter(item => item.id === categoryRegion[0]);
	  			 the_item_name = the_item[0]['name'];
	  		}
	  		let p = the_item_name.indexOf('.');
	  		the_item_name = the_item_name.substr(0, p);
	  		
		    return (
		      <div>
		        <Modal title="" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)} footer={this.state.orientation !== "2" ? 
		        	[
		        		<Button key="chart_options" onClick={()=>this.setState({chartOptionsVisible: true})}></Button>,
		            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}> </Button>,
		            <Button key="submit" type="primary" loading={this.state.loading} onClick={this.handleOk.bind(this)}>
		               
		            </Button>,
	          	]		        	
		        	:
		        	[
		        		<Button key="set_series" onClick={()=>this.setState({yOptionsVisible: true})}></Button>,
		        		<Button key="chart_options" onClick={()=>this.setState({chartOptionsVisible: true})}></Button>,
		            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}> </Button>,
		            <Button key="submit" type="primary" loading={this.state.loading} onClick={this.handleOk.bind(this)}>
		               
		            </Button>,
	          ]} width="800px" style={{ top: 20 }}>
		          	<div>
		          			<Row>
		          					<Col span={16}>
				          					<Row style={{margin: '5px'}}>
						          					<Col>
								          					<label>
										          					<RadioGroup value={this.state.orientation} onChange={(e)=>this.setState({orientation: e.target.value})}>
										      									<RadioButton value="1" title=""></RadioButton>
										      									<RadioButton value="2" title=""></RadioButton>
										      							</RadioGroup>
								      							</label>
						      							</Col>
				      							</Row>
				      							<Row style={{margin: '5px'}}>
						          					<Col>
								      							<label>
								      									<Select value={this.state.dataRegion} style={{width: 300}} onChange={(value) => {
								      											let yOptions = [];
								      											if(value.length > 0){
								      													value.map(y => items.map(item => item.id === y ? yOptions.push({name: item.name, type: ''}) : null));
								      											}
								      											this.setState({dataRegion: value, yOptions});
								      										}} mode="multiple">
								      											{
								      												items.map(item => (item.name.indexOf('.') !== -1 && (the_item_name === "" || item.name.indexOf(the_item_name + '.') !== -1)) ?
								      												 		<Option value={item.id} key={item.id}>{item.name}</Option> : null
								      												)
								      											}								      											
								      									</Select>
								      							</label>
							      						</Col>
				      							</Row>
				      							<Row style={{margin: '5px'}}>
						          					<Col>
								      							<label>
								      									<Select value={this.state.categoryRegion} style={{width: 300}} onChange={(value) => this.setState({categoryRegion: value})} mode="multiple">
								      											{
								      												items.map(item => (item.name.indexOf('.') !== -1 && (the_item_name === "" || item.name.indexOf(the_item_name + '.') !== -1)) ?
								      												 		<Option value={item.id} key={item.id}>{item.name}</Option> : null
								      												)
								      											}								      											
								      									</Select>								      							
								      							</label>
							      						</Col>
				      							</Row>
		          					</Col>
		          					<Col span={8}>
				      							<Row>
						          					<Col>
								      							<label>
								      									<Input value={this.state.title} onChange={(e) => this.setState({title: e.target.value})}/>
								      							</label>
							      						</Col>
				      							</Row>
				      							<Row>
						          					<Col>
								      							<label>X
								      									<Input value={this.state.x} onChange={(e) => this.setState({x: e.target.value})}/>
								      							</label>
							      						</Col>
				      							</Row>
				      							<Row>
						          					<Col>
								      							<label>Y
								      									<Input value={this.state.y} onChange={(e) => this.setState({y: e.target.value})}/>
								      							</label>
							      						</Col>
				      							</Row>
				      							<Row style={{'margin-top': '4px'}}>
						          					<Col>
								      							<label>{/*<Checkbox value={this.state.is3D} onChange={(e) => this.setState({is3D: e.target.checked})}>3D</Checkbox>*/}
								      									<Select value={this.state.chartType} onChange={(value) => this.setState({chartType: value})} style={{width:'120px'}}>
																		      	<Option value="column"></Option>
																		      	<Option value="stackedColumn"></Option>
																		      	<Option value="bar"></Option>
																		      	<Option value="stackedBar"></Option>
																		      	<Option value="line"></Option>
																		      	<Option value="pie"></Option>
																		      	<Option value="area"></Option>
																		      	<Option value="stackedArea"></Option>
																		      	{/*<Option value="funnel"></Option>*/}
																		      	<Option value="polar"></Option>
																		      	<Option value="gauge"></Option>
																		    </Select>
								      							</label>
							      						</Col>
				      							</Row>
		          					</Col>
		          			</Row>
		          	</div>
		        </Modal>
		        <XOptions visible={this.state.xOptionsVisible} xOptions={this.state.xOptions} owner={this}/>
		      	<YOptions visible={this.state.yOptionsVisible} yOptions={this.state.yOptions} owner={this} items={items}/>
		        <ChartOptions visible={this.state.chartOptionsVisible} chartOptions={this.state.chartOptions} owner={this}/>
		      </div>
		    );
	  }
	  
    componentDidMount(){ return;
    		if($('#chart_container').length > 0){
		    		Highcharts.chart('chart_container', {				    						
								chart: {
										type: 'column'
								},
								title: {
				            text: ''
				        },
				        xAxis: {
				            categories: []
				        },
				        series: [],
				        credits: {enabled: false}
						});
				}
		}
		
    componentDidUpdate(prevProps, prevState) { return;
    		if(this.state.visible && $('#chart_container').length > 0){
    				if(this.state.dataRegion.search(/^([A-Z]+[0-9]+)(:)([A-Z]+[0-9]+)$/)!==-1 && this.state.categoryRegion.search(/^([A-Z]+[0-9]+)(:)([A-Z]+[0-9]+)$/)!==-1){
					  		let chart_settings = this.owner.owner.excel ? this.owner.owner.excel.getChartSettings(this.state) : {				    						
										chart: {
												type: 'column'
										},
										title: {
						            text: ''
						        },
						        xAxis: {
						            categories: []
						        },
						        series: [],
						        credits: {enabled: false}
						    };
					  		Highcharts.chart('chart_container', chart_settings);
			  		}else if(this.state.dataRegion === '' && this.state.categoryRegion === ''){
			  				Highcharts.chart('chart_container', {				    						
										chart: {
												type: 'column'
										},
										title: {
						            text: ''
						        },
						        xAxis: {
						            categories: []
						        },
						        series: [],
						        credits: {enabled: false}
								});
			  		}
	  		}
		}
}

export default ChartProperty;