export const fontFamilySize = [8,9,10,11,12,14,16,18,20,22,24,26,28,36,42,48,72]

export function fontEnToCn(en) {
    let s = en
    $.each(fontFamilyList, (i, font) => {
        if(en.indexOf(font.en) !== -1){
            s = font.ch
            return true
        }
    })
    return s
}